.promo {
    padding-top: 155px;
    padding-bottom: 20px;
    background-color: #e7f1f5;
    background-image: url(../img/promo-mobile.png);
    background-position: center bottom;
    background-repeat: no-repeat;
    transition: 0.3s;

    @media (min-width: @screen-lg) {
        padding-top: 250px;
        padding-bottom: 130px;
        background-image: url(../img/promo-bg.png);
        background-size: 100%;
        background-position: left bottom;
    }

    &--hidden {
        display: none;
    }

    &__inner {
        .container;
        position: relative;
        &::before {
            @media (min-width: @screen-lg) {
                content: '';
                position: absolute;
                bottom: -130px;
                right: -30px;
                width: 599px;
                height: 626px;
                background-image: url(../img/promo.png);
            }
        }
    }
    &__title {
        font-size: 20px;
        font-family: 'Roboto Slab', sans-serif;
        font-weight: 400;

        @media (min-width: @screen-lg) {
            font-size: @h1-size;
        }
    }
    &__subtitle {
        margin-bottom: 20px;
        font-family: 'Roboto Slab', sans-serif;
        font-size: 20px;
        font-weight: 300;
        
        @media (min-width: @screen-lg) {
            font-size: @h1-size;
            margin-bottom: 25px;
        }
    }
    &__description {
        margin-bottom: 50px;
        font-size: 14px;
        line-height: 20px;

        @media (min-width: @screen-lg) {
            width: calc(5 / 12 * 100%);
            margin-bottom: 30px;
            font-size: @accent-size;
            line-height: 30px;
        }
    }
    &__btn {  
        display: block;
        margin: 0 auto;
        margin-bottom: 50px; 
        @media (min-width: @screen-lg) {
            margin: 0;
            margin-bottom: 30px; 
        }
    }
    &__raiting {

        @media (min-width: @screen-lg) {
            position: relative;
            display: flex;
            flex-direction: column;
        }

    }
    &__raiting-item {
        display: flex;
        align-items: center;
        text-decoration: none;
        &:not(:last-of-type) {
            margin-bottom: 10px;
        }
        &--tripadvisor {
            .promo__raiting-company {
                width: 132px;
                height: 20px;
                background-image: url(../img/tripadvisor.png);
            }
        }
        &--booking {
            .promo__raiting-company {
                width: 113px;
                height: 19px;
                background-image: url(../img/booking.png);
            }
        }
    }
    &__raiting-company {
        background-repeat: no-repeat;
        margin-right: 45px;
    }
    &__raiting-value {
        font-size: 18px;
        color: @secondary;
        font-weight: 700;
        @media (min-width: @screen-lg) {
            font-size: 24px;
        }
    }
    &__price {
        display: none;
    }
}