.not-found {
    &__inner {
        max-width: @screen-xs;
        margin: 0 auto;
        padding: 0 20px;
        position: relative;
        font-family: 'Roboto Slab', sans-serif;

        @media(min-width: @screen-sm){
            padding: 0 15px;
        }
        @media(min-width: @screen-lg){
            .container;
        }

        &::before {
            content: '404';
            position: absolute;
            top: 90px;
            right: 0;
            left: 0;
            right: 0;
            margin: 0 auto;
            padding: 0 20px;
            height: 125px;
            color: @primary;
            font-weight: 700;
            line-height: 115px;
            font-size: 165px;

            @media (min-width: @screen-lg) {
                width: 100%;
                top: 50px;
                height: 600px;
                font-size: 710px;
                line-height: 495px;
            }
        }
        &::after {
            content: '';
            position: absolute;
            top: 115px;
            right: 10px;
            width: 182px;
            height: 339px;
            background-repeat: no-repeat;
            background-image: url(../img/traveler.png);
            background-size: cover;
            @media (min-width: @screen-lg) {
                top: 90px;
                right: 120px;
                width: 432px;
                height: 810px;
            }
        }
    }
    &__text {
        padding-top: 45px;
        font-size: 24px;
        font-weight: 700;

        @media (min-width: @screen-lg) {
            padding-top: 710px;
            font-size: 60px;
        }
    }
}