.faq {
    padding-top: 50px;
    padding-bottom: 70px;
    background-color: #4A5583;
    color: @white;
    overflow: hidden;

    &__inner {
        .container;
        position: relative;
        @media (min-width: @screen-lg) {
            &::before {
                content: '';
                position: absolute;
                bottom: -85px;
                right: -235px;
                width: 671px;
                height: 635px;
                background-image: url(../img/faq.png);
                background-repeat: no-repeat;
            }
        }
    }
    &__questions {
        .row();
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        align-items: flex-start;
        position: relative;
        z-index: 2;
    }
    &__question {
        margin-bottom: 60px;
        .col-(12);

        @media (min-width: @screen-lg) {
            margin-bottom: 50px;
            &:nth-child(2n+1) {
                .col-offset-(1);
                .col-(5);
            }
            &:nth-child(2n) {
                .col-(6);
            }
        }
        }
    &__question-question {
        margin-bottom: 15px; 
        font-weight: 500;
        font-size: 14px;

        @media (min-width: @screen-lg) {
            font-size: @accent-size;
        }
    }
    &__question-answer {
        font-size: 14px;

        @media (min-width: @screen-lg) {
            font-size: @text-size;
            max-width: 350px;
        }
        & a {
            color: @primary;
        }
    }
    &__back {
        display: none;
        @media (min-width: @screen-lg) {
            display: block;
            bottom: -70px;
        }
    }
}