  font-style: normal;
}
.toggler {
  z-index: 1;
  width: 32px;
  height: 28px;
  cursor: pointer;
}
.toggler span {
  display: inline-block;
  position: relative;
  width: 100%;
  height: 5px;
  background-color: #fb9972;
  border-radius: 2px;
  transition: 0.3s;
}
.toggler span:before,
.toggler span:after {
  position: absolute;
  content: '';
  left: 0px;
  right: 0;
  width: 100%;
  height: 5px;
  background-color: #fb9972;
  border-radius: 2px;
  transition: height 0.1s,
                        transform 0.3s;
}
.toggler span:before {
  top: -9px;
}
.toggler span:after {
  top: 9px;
}
.toggler--close span {
  height: 0;
}
.toggler--close span:before,
.toggler--close span:after {
  top: 0;
}
.toggler--close span:before {
  transform: rotate(45deg);
}
.toggler--close span:after {
  transform: rotate(-45deg);
}
@media (min-width: 1200px) {
  .toggler {
    display: none;
  }
}
